"use client"

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts"
import { useWasteData } from "@/context/waste-data-context"
import { useMemo } from "react"

export function LgaBarChart() {
  const { submissions } = useWasteData()
  
  const chartData = useMemo(() => {
    const today = new Date().toISOString().split("T")[0]
    const lgaTotals: { [key: string]: number } = {}
    
    submissions
      .filter(s => s.date === today)
      .forEach(submission => {
        lgaTotals[submission.lga] = (lgaTotals[submission.lga] || 0) + submission.tons
      })
    
    return Object.entries(lgaTotals)
      .map(([lga, tons]) => ({ lga, tons: Math.round(tons * 10) / 10 }))
      .sort((a, b) => b.tons - a.tons)
      .slice(0, 10)
  }, [submissions])

  return (
    <Card className="border-0 shadow-sm">
      <CardHeader className="pb-2">
        <CardTitle className="font-serif text-lg text-primary">
          {"Today's Top 10 LGAs by Tonnage"}
        </CardTitle>
      </CardHeader>
      <CardContent>
        <div className="h-[300px]">
          <ResponsiveContainer width="100%" height="100%">
            <BarChart data={chartData} layout="vertical" margin={{ top: 5, right: 20, left: 80, bottom: 5 }}>
              <CartesianGrid strokeDasharray="3 3" stroke="hsl(var(--border))" horizontal={true} vertical={false} />
              <XAxis 
                type="number"
                tick={{ fontSize: 12, fill: "hsl(var(--muted-foreground))" }}
                tickLine={false}
                axisLine={{ stroke: "hsl(var(--border))" }}
                tickFormatter={(value) => `${value}t`}
              />
              <YAxis 
                type="category"
                dataKey="lga"
                tick={{ fontSize: 11, fill: "hsl(var(--muted-foreground))" }}
                tickLine={false}
                axisLine={{ stroke: "hsl(var(--border))" }}
                width={75}
              />
              <Tooltip 
                contentStyle={{ 
                  backgroundColor: "hsl(var(--card))",
                  border: "1px solid hsl(var(--border))",
                  borderRadius: "var(--radius)"
                }}
                formatter={(value: number) => [`${value.toFixed(1)} tons`, "Collected"]}
              />
              <Bar 
                dataKey="tons" 
                fill="hsl(var(--chart-2))" 
                radius={[0, 4, 4, 0]}
              />
            </BarChart>
          </ResponsiveContainer>
        </div>
      </CardContent>
    </Card>
  )
}
